.launcher-container {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  background-color: rgba(8, 13, 25, 0.7);
  will-change: transform;
  transition: 0.3s ease transform; }
  .launcher-container.close {
    transform: translateY(420px); }

.launcher-slide {
  display: flex;
  position: relative;
  height: 420px;
  padding: 40px 0 0 38px;
  will-change: transform;
  transition: 0.3s ease transform; }

.launcher-search {
  position: relative;
  width: 104px;
  padding-top: 60px;
  margin-right: 42px; }
  .launcher-search div {
    position: relative;
    border-radius: 50%;
    width: 100%;
    height: 104px;
    background: url("../images/common/ic-search-grey.png") 50% 50% no-repeat;
    background-size: 40px 40px;
    font-size: 0;
    color: rgba(0, 0, 0, 0); }
    .launcher-search div.focus {
      background-color: rgba(255, 255, 255, 0.1);
      background-image: url("../images/common/ic-search-white.png");
      box-shadow: 0 0 0 6px #eee; }

.launcher-profile {
  position: relative;
  width: 274px;
  padding-top: 65px; }
  .launcher-profile > div {
    position: relative;
    padding-top: 110px; }
    .launcher-profile > div::after {
      content: '';
      position: absolute;
      top: 70px;
      left: 4px;
      width: 84px;
      height: 32px;
      margin: 0 auto;
      background-image: url("../images/common/badge-my-kr.png");
      background-size: cover; }
    .launcher-profile > div .ic-profile {
      position: absolute;
      top: 0;
      left: 0;
      width: 92px;
      height: 92px; }
    .launcher-profile > div .launcher-profile-nick {
      font-size: 3.6rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; }
    .launcher-profile > div .launcher-profile-coupon {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #ef3484;
      top: -5px;
      left: 56px;
      height: 44px;
      padding: 0 12px 0 12px;
      border-radius: 22px;
      font-size: 3.2rem; }
    .launcher-profile > div.focus .ic-profile {
      box-shadow: 0 0 0 6px #eee; }

.launcher-indicator-home {
  position: absolute;
  bottom: 32px;
  right: 34px;
  padding-left: 72px;
  font-size: 2.4rem;
  color: #626373; }
  .launcher-indicator-home::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 62px;
    height: 34px;
    background-image: url("../images/common/ic-key-down.png");
    background-size: cover; }

.launcher-contents h2 {
  margin-bottom: 20px;
  font-size: 3.2rem;
  color: #eee; }

.launcher-contents > ul {
  display: flex; }
  .launcher-contents > ul > li {
    position: relative;
    margin-right: 16px; }
    .launcher-contents > ul > li:last-child {
      margin-right: 0; }
    .launcher-contents > ul > li.focus {
      z-index: 5; }

.launcher-contents-watching {
  margin-right: 72px; }

.launcher-contents-recommend ul {
  padding-right: 60px; }
